import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';

export interface IRecord {
  id: string;
  description: string;
  expandable: boolean;
  level: number;
  isExpanded?: boolean;
}

@Component({
  selector: 'app-test-child',
  templateUrl: './child.component.html',
  styleUrls: ['./child.component.scss']
})
export class ChildComponent {
  public tableRowHeight: 48;
  public tableDef = {
    columns: [
      { title: 'Id', key: 'id' },
      { title: 'Description', key: 'description' }
    ]
  };
  public recordset$ = new BehaviorSubject<IRecord[]>([]);

  constructor(
    private router: Router
  ) {
    this.getRecords();
  }

  public onNavigate(route: string) {
    this.router.navigate([route]);
  }

  public toggleLeaf(record: IRecord) {
    const spliceIndex = this.recordset$.value.indexOf(record) + 1;
    const records = [...this.recordset$.value];

    if (!record.isExpanded) {
      const level = record.level + 1;
      const leafRecords: IRecord[] = [];
      for (let i = 0; i < 10; i++) {
        leafRecords.push({ id: `${level}-${i}`, description: `${level}-${i} description`, expandable: true, level });
      }
      records.splice(spliceIndex, 0, ...leafRecords);
    } else {
      let deleteCount = 0;
      for (let i = spliceIndex; i < records.length; i++) {
        if (records[i].level <= record.level) {
          break;
        }
        deleteCount += 1;
      }
      records.splice(spliceIndex, deleteCount);
    }
    record.isExpanded = !record.isExpanded;
    this.recordset$.next(records);
  }

  private getRecords() {
    const startIndex = this.recordset$.value.length;
    const records = [...this.recordset$.value];
    for (let i = startIndex; i < startIndex + 10; i++) {
      records.push({ id: `0-${i}`, description: `0-${i} description`, expandable: true, level: 0 });
    }
    this.recordset$.next(records);
  }

}
