import { Component, computed, effect, signal } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, tap, takeUntil, Subject, BehaviorSubject, startWith, pairwise, map } from 'rxjs';
import { AppCacheService } from 'src/app/app-cache.service';
import { AppDataService } from 'src/app/app-data.service';

@Component({
  selector: 'app-test-parent',
  templateUrl: './parent.component.html',
  styleUrls: ['./parent.component.scss']
})
export class ParentComponent {
  public recordset = signal<{ id: number; description: string; }[]>([{ id: -1, description: '' }]);
  public record = signal<{ id: number; description?: string; }>({ id: -1 });
  public recordset$ = new BehaviorSubject<{ id: number; description: string; }[]>([]);
  public record$ = new BehaviorSubject<{ id: number; description?: string; }>({ id: -1 });
  public recordsetTotal = 0;
  public recordsetTotalComputed = computed(() => {
    console.log('computed');
    return this.recordset().length;
  });

  constructor(
    private router: Router,
    private appDataService: AppDataService,
    private appCache: AppCacheService
  ) {
    effect(() => {
      console.log('effect');
      this.recordsetTotal = this.recordset().length;
    });

    this.recordset$.pipe(
      startWith(undefined),
      pairwise(),
      map(([prev, curr]) => ({ prev, curr }))
    ).subscribe((o) => {
      console.log(o);
    });

    this.record$.pipe(
      startWith(undefined),
      pairwise(),
      map(([prev, curr]) => ({ prev, curr }))
    ).subscribe((o) => {
      console.log(o);
    });
  }

  public onNavigate(route: string) {
    this.router.navigate([route]);
  }

  public onMakeRequest() {
    // this.getLongRequest().subscribe();

    this.recordset()[0].description = 'description';
    this.record().description = 'description';
    // this.recordset.update(v => v);
    // this.recordset.mutate(v => {
    //   const startIndex = v.length;
    //   const endIndex = startIndex + 100;
    //   for (let i = startIndex; i < endIndex; i++) {
    //     v.push({ id: i, description: `${i} - Description` });
    //   }
    // });

    const records = [...this.recordset$.value];
    const startIndex = this.recordset$.value.length;
    const endIndex = startIndex + 100;
    for (let i = startIndex; i < endIndex; i++) {
      records.push({ id: i, description: `${i} - Description` });
    }
    this.recordset$.next(records);
    const record = { id: startIndex, description: `${startIndex} - Description` };
    this.record$.next(record);
  }

  private getLongRequest(): Observable<any> {
    return this.appDataService.getLongRequest().pipe(
      tap({
        next: result => console.log(result),
        error: error => console.log(error)
      }),
      takeUntil(this.appCache.cancelHttpRequests)
    );
  }

}
